@import '../themes.scss';

a {
    text-decoration: none;

    @include themed() {
        color: t($color);
    }
}

a:visited {
    text-decoration: none;
}

.hr {
    width: 75%;
    height: 4px;
    margin: auto;
    margin-top: 20px;
    margin-bottom: 70px;
    -webkit-clip-path: polygon(3% 0px, 100% 0px, 97% 100%, 0px 100%);
    clip-path: polygon(3% 0px, 100% 0px, 97% 100%, 0px 100%);

    @include themed() {
        background-color: t($color);
    }
}

h3 {
    font-family: Roboto;
    font-weight: 300;
}

h2 {
    font-size: $font-smaller;
    font-weight: 500;
}

#cv-wrapper {
    height: 100%;
    width: 60%;
    min-width: 340px;
    border-radius: 10px;
    font-family: $font;
    font-weight: 600;
    margin: 50px;
    padding-bottom: 50px;
    -webkit-clip-path: polygon(3% 0px, 100% 0px, 97% 100%, 0px 100%);
    clip-path: polygon(3% 0px, 100% 0px, 97% 100%, 0px 100%);
    user-select: none;
    -moz-user-select: none;
    -webkit-user-drag: none;

    @include themed() {
        color: t($bg);
        border: 5px solid t($border);
        color: t($color);
    }
}

#cv-nav {
    width: 80%;
    display: flex;
    justify-content: space-around;
}

.cv-nav-link {
    text-decoration: none;

    @include themed() {
        color: t($color);
    }
}

#cv-info {
    height: 100%;
    width: 90%;
    margin: auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 5px;
    margin-top: 10px;
}

#cv-pic {
    width: 150px;
    height: 150px;
    -webkit-user-drag: none;
    border-radius: 10px;

    @include themed() {
        color: t($bg);
    }
}

#cv-bio {
}

.bio-contact-container {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.bio-contact-container a {
    margin-left: 10px;
}

#cv-contact {
    font-family: $font;
    text-align: left;

    @include themed() {
        color: t($color);
    }
}

#cv-description {
    font-family: $font;
    height: 100%;
    margin: 50px;

    @include themed() {
        color: t($color);
    }
}

#cv-languages {
    font-family: $font;
    display: flex;
    flex-direction: row;
    justify-content: space-around;

    @include themed() {
        color: t($color);
    }
}

.language-level {
    font-family: $font-light;
    font-weight: 200;
    font-size: $font-small;
    animation: 3s cubic-bezier(0.445, 0.05, 0.55, 0.95)
}

#skills {
    width: 80%;
    margin: auto;
    margin-bottom: 50px;
}

.cv-skill {
    font-family: $font;
    text-align: left;
    text-indent: 30px;
    height: 51px;

    @include themed() {
        color: t($color);
    }
}

.cv-subskill {
    font-family: $font;
    font-size: 12px;
    text-indent: 50px;

    @include themed() {
        color: t($color);
    }
}

#cv-hobbies {
    text-align: left;
    text-indent: 50px;
}

#cv-formation {
    text-align: left;
    margin-bottom: 50px;
}

#projects {
    margin-bottom: 50px;
}

.project {
    margin: auto;
    margin-top: 25px;
    width: 80%;
    border-radius: 3px;
}

.project-image {
    position: relative;
    height: 125px;
    width: 100%;
}

.project-image-inside {
    font-family: Roboto;
    position: relative;
    line-height: 150px;
    font-size: $font-bigger;
    font-weight: 700;
    color: #ccc;
    -webkit-text-stroke: 2px #333;
}

.project-language-container {
    display:flex;
    flex-flow: row wrap;
    justify-content:center;
    align-content:center;
    align-items:center;
    font-weight: 400;
    font-size: $font-small;
}

.project-description {
    font-family: "Roboto", sans-serif;
    font-weight: lighter;
    font-size: $font-small;
    text-align: center;
    margin-left: 10px;
    margin-right: 10px;
    margin-bottom: 50px;
    margin-top: 0px;
    
    @include themed() {
        color: t($color);
    }
}

@media (max-width: 600px) {
    #cv-wrapper {
        width: 95%;
    }
    #cv-info {
        flex-direction: column;
        align-items: center;
    }
}

@media (max-width: 1000px) {
    #cv-wrapper {
        width: 95%;
    }
    #cv-info {
        width: 100%;
        justify-content: space-between;
    }
}

@media (min-width: 1000px) and (max-width: 1400px) {
    #cv-wrapper {
        width: 80%;
    }
}