@import './themes.scss';

#App {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow-x: hidden;
  width: 100%;
  height: minmax(100vh, 100%);
  min-height: 100vh;

  @include themed() {
    background: t($bg);
  }
}

#App-navbar {
  display: flex;
  flex-direction: row;
  width: 300px;
  justify-content: space-between;
  
  @include themed() {
    color: t($color);
  }
}

#App-links p {
  margin: 10px;
}

#App-header {
  position: fixed;
  top: 10px;
  left: 0;
  z-index: 1000;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.App-header-button {
  z-index: 1000;
}

#footer {
  @include themed() {
    color: t($color);
  }
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #333;
}

::-webkit-scrollbar-thumb {
  background: #ccc;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}