$font: "Montserrat", sans-serif;
$font-light: "Montserrat Light", sans-serif;

$font-smaller: 12px;
$font-small: 14px;
$font-medium: 16px;
$font-big: 18px;
$font-bigger: 32px;

$bg: "bg";
$hover-bg: "hover-bg";
$important-bg: "important-bg";
$secondary-bg: "secondary-bg";
$color: "color";
$hover-color: "hover-color";
$text: "text";
$border: "border";
$scrollbar-bg: "scrollbar-bg";
$scrollbar-fg: "scrollbar-fg";
$scrollbar-hover: "scrollbar-hover";
$cred: "cred";
$cgreen: "cgreen";
$cblue: "cblue";

$hover-bg--dark: #777;
$hover-bg--light: #777;

// night mode
$bg--dark: #333;
$important-bg--dark: #833;
$secondary-bg--dark: #111;
$color--dark: #ccc;
$hover-color--dark: #ddd;
$text--dark: #ccc;
$border--dark: #ccc;
$scrollbar-bg--dark: #333;
$scrollbar-fg--dark: #ccc;
$scrollbar-hover--dark: #ddd;
$cred--dark: #833;
$cgreen--dark: #383;
$cblue--dark: #338;

// day mode
$bg--light: #ccc;
$important-bg--light: #b00;
$secondary-bg--light: #ddd;
$color--light: #333;
$hover-color--light: #444;
$text--light: #333;
$border--light: #333;
$scrollbar-bg--light: #ccc;
$scrollbar-fg--light: #333;
$scrollbar-hover--light: #444;
$cred--light: #833;
$cgreen--light: #383;
$cblue--light: #338;

$themes: (
    dark: (
        bg: $bg--dark,
        hover-bg: $hover-bg--dark,
        important-bg: $important-bg--dark,
        secondary-bg: $secondary-bg--dark,
        color: $color--dark,
        hover-color: $hover-color--dark,
        text: $text--dark,
        border: $border--dark,
        scrollbar-bg: $scrollbar-bg--dark,
        scrollbar-fg: $scrollbar-fg--dark,
        scrollbar-hover: $scrollbar-hover--dark,
        cred: $cred--dark,
        cgreen: $cgreen--dark,
        cblue: $cblue--dark
    ),
    light: (
        bg: $bg--light,
        hover-bg: $hover-bg--light,
        important-bg: $important-bg--light,
        secondary-bg: $secondary-bg--light,
        color: $color--light,
        hover-color: $hover-color--light,
        text: $text--light,
        border: $border--light,
        scrollbar-bg: $scrollbar-bg--light,
        scrollbar-fg: $scrollbar-fg--light,
        scrollbar-hover: $scrollbar-hover--light,
        cred: $cred--light,
        cgreen: $cgreen--light,
        cblue: $cblue--light
    )
);

@mixin themed() {
    @each $theme, $map in $themes {
        .theme--#{$theme} & {
            $theme-map: () !global;
            @each $key, $submap in $map {
                $theme-map: map-merge($theme-map, ($key: $submap)) !global;
            }
            @content;
                $theme-map: null !global;
        }
    }
}
  
@function t($key) {
    @return map-get($theme-map, $key);
}

@mixin color-fade-mixin($name) {
    @keyframes #{$name} {
        from {
        }
        to {
            background: map-get($theme-map, $hover-bg);
        }
    }
}