@import "../themes.scss";

@-webkit-keyframes grow-40 {
    from {
        transform-origin: top;
        transform: translateY(-40px);
    }
    to {
        transform: translateY(0px);
    }
}

@keyframes grow-40 {
    from {
        transform-origin: top;
        transform: translateY(-40px);
    }
    to {
        transform: translateY(0px);
    }
}

@-webkit-keyframes shrink-40 {
    from {
        transform-origin: top;
        transform: translateY(0px);
    }
    to {
        transform: translateY(-40px);
    }
}

@keyframes shrink-40 {
    from {
        transform-origin: top;
        transform: translateY(0px);
    }
    to {
        transform: translateY(-40px);
    }
}

.fade-in-40 {
    -webkit-animation: grow-40 0.5s forwards ease;
    animation: grow-40 0.5s forwards ease;
    -webkit-animation-iteration-count: 1;
    animation-iteration-count: 1;
}

.fade-out-40 {
    -webkit-animation: shrink-40 0.5s forwards ease;
    animation: shrink-40 0.5s forwards ease;
    -webkit-animation-iteration-count: 1;
    animation-iteration-count: 1;
}

@-webkit-keyframes grow-80 {
    from {
        transform-origin: top;
        transform: translateY(-80px);
    }
    to {
        transform: translateY(0px);
    }
}

@keyframes grow-80 {
    from {
        transform-origin: top;
        transform: translateY(-80px);
    }
    to {
        transform: translateY(0px);
    }
}

@-webkit-keyframes shrink-80 {
    from {
        transform-origin: top;
        transform: translateY(0px);
    }
    to {
        transform: translateY(-80px);
    }
}

@keyframes shrink-80 {
    from {
        transform-origin: top;
        transform: translateY(0px);
    }
    to {
        transform: translateY(-80px);
    }
}

.fade-in-80 {
    -webkit-animation: grow-80 0.5s forwards ease;
    animation: grow-80 0.5s forwards ease;
    -webkit-animation-iteration-count: 1;
    animation-iteration-count: 1;
}

.fade-out-80 {
    -webkit-animation: shrink-80 0.5s forwards ease;
    animation: shrink-80 0.5s forwards ease;
    -webkit-animation-iteration-count: 1;
    animation-iteration-count: 1;
}

.header {
    height: 80px;
    width: 100%;
    line-height: 40px;
    padding: 20px 5vw;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    z-index: 1000;

    @include themed() {
        background: t($secondary-bg);
        border-bottom: 1px solid t($border);
        color: t($color);
    }
}

.header a {
    color: inherit;
    text-decoration: none;
}

.subHeader {
    height: 30px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    line-height: 30px;
    z-index: 999;

    @include themed() {
        background: t($secondary-bg);
        border-bottom: 1px solid t($border);
        color: t($color);
    }
}

.header-link {
    width: auto;
    height: auto;
    cursor: pointer;
}

.header-sub-link {
    width: auto;
    height: auto;
    cursor: pointer;
}

.subLinks {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    height: inherit;
    font-family: 'Montserrat Light', 'Monospace Regular', Arial, Helvetica, sans-serif;
    -webkit-touch-callout:  none; /* iOS Safari */
    -webkit-user-select:    none; /* Safari */
     -khtml-user-select:    none; /* Konqueror HTML */
       -moz-user-select:    none; /* Old versions of Firefox */
        -ms-user-select:    none; /* Internet Explorer/Edge */
            user-select:    none;
}

.subLinks * {
    text-decoration: none;
    
    @include themed() {
        color: t($color);
    }
}

.subLinks *:not(:first-child) {
    margin-left: 30px;
}

.subLinks *:hover {
    color: #22bbff;
}

.links {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    height: inherit;
    font-family: 'Montserrat Medium', 'Monospace Regular', Arial, Helvetica, sans-serif;
    -webkit-touch-callout:  none; /* iOS Safari */
    -webkit-user-select:    none; /* Safari */
     -khtml-user-select:    none; /* Konqueror HTML */
       -moz-user-select:    none; /* Old versions of Firefox */
        -ms-user-select:    none; /* Internet Explorer/Edge */
            user-select:    none;
}

.links * {
    text-decoration: none;
}

.links *:not(:first-child) {
    margin-left: 30px;
}

.links *:hover {
    color: #22bbff;
}

#title {
    font-family: 'Montserrat', 'montserrat', 'sans-serif-light', 'Monospace Regular', Arial, Helvetica, sans-serif;
    font-size: 50px;
    font-weight: 400;
    -webkit-touch-callout:  none; /* iOS Safari */
    -webkit-user-select:    none; /* Safari */
     -khtml-user-select:    none; /* Konqueror HTML */
       -moz-user-select:    none; /* Old versions of Firefox */
        -ms-user-select:    none; /* Internet Explorer/Edge */
            user-select:    none;
    cursor: pointer;
}

#title:hover {
    color: #22bbff;
}

.stickyHeader {
    height: 40px;
    width: 100%;
    line-height: 40px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 0;
    border-bottom: solid;
    border-bottom-width: 1px;
    z-index: 100;
    
    @include themed() {
        background: t($secondary-bg);
        border-bottom: 1px solid t($border);
        color: t($color);
    }
}

.stickyHeader a {
    color: inherit;
    text-decoration: none;
    cursor: pointer;
}

.stickyHeader a:hover {
    color: #22bbff;
}

.stickySubHeader {
    height: 30px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    line-height: 30px;
    position: fixed;
    top: 40px;
    left: 0;
    background-color: white;
    z-index: 99;
    
    @include themed() {
        background: t($secondary-bg);
        border-bottom: 1px solid t($border);
        color: t($color);
    }
}

#stickyTitle {
    font-family: 'Montserrat', 'montserrat', 'sans-serif-light', 'Monospace Regular', Arial, Helvetica, sans-serif;
    font-size: 20px;
    font-weight: 400;
    -webkit-touch-callout:  none; /* iOS Safari */
    -webkit-user-select:    none; /* Safari */
     -khtml-user-select:    none; /* Konqueror HTML */
       -moz-user-select:    none; /* Old versions of Firefox */
        -ms-user-select:    none; /* Internet Explorer/Edge */
            user-select:    none;
}

#sticky-header-icons {
    display: flex;
    flex-direction: row;
    position: absolute;
    right: 0;
    height: 40px;
}

.sticky-header-icon {
    height: 40px;
    display: flex;
    align-items: center;
    width: 40px;
    cursor: pointer;
}

@media (max-width: 600px) {
    .subHeader {
        display: none;
    }
    .stickySubHeader {
        display: none;
    }
}